import { useState, useEffect, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import './CapsuleCards.scss';

// Import card images
import legacyairdropImage from '../assets/cardsinsideimage/legacyairdrop.jpeg';
import upornahImage from '../assets/cardsinsideimage/upornah.jpeg';
import burnImage from '../assets/cardsinsideimage/burn.jpeg';
import presaleImage from '../assets/cardsinsideimage/presale.jpeg';
import missionImage from '../assets/cardsinsideimage/missions.jpeg';
import pvpcardImage from '../assets/cardsinsideimage/pvpcard.jpeg';

const CapsuleCards = () => {
  const [activeIndex, setActiveIndex] = useState(0);
  const [swipeDirection, setSwipeDirection] = useState(null);
  const [isAnimating, setIsAnimating] = useState(false);
  const [isMobile, setIsMobile] = useState(window.innerWidth <= 768);
  const containerRef = useRef(null);
  const navigate = useNavigate();
  
  // Handle window resize and update isMobile state
  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 768);
    };
    
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);
  
  // Reset state when component mounts to ensure clean state after navigation
  useEffect(() => {
    setActiveIndex(0);
    setSwipeDirection(null);
    setIsAnimating(false);
  }, []);
  
  // Card data
  const cards = [
    {
      id: 1,
      title: "Up Or Nah",
      color: "#FF5722",
      image: upornahImage,
      url: "betting.gotchanft.com",
      status: "available"
    },
    {
      id: 2,
      title: "Legacy Airdrop",
      color: "#2196F3",
      image: legacyairdropImage,
      url: "airdrop.gotchanft.com",
      status: "available"
    },
    {
      id: 3,
      title: "Burn",
      color: "#E91E63",
      image: burnImage,
      url: "burn.gotchanft.com",
      status: "available"
    },
    {
      id: 4,
      title: "Presale",
      color: "#9C27B0",
      image: presaleImage,
      path: "/",
      status: "coming-soon"
    },
    {
      id: 5,
      title: "Gotcha Missions",
      color: "#4CAF50",
      image: missionImage,
      path: "/mint",
      status: "available"
    },
    {
      id: 6,
      title: "PvP Card Game",
      color: "#FFC107",
      image: pvpcardImage,
      path: "/",
      status: "coming-soon"
    }
  ];

  // Handle next card
  const nextCard = () => {
    if (isAnimating) return;
    setIsAnimating(true);
    setSwipeDirection('left');
    
    setTimeout(() => {
      setActiveIndex((prevIndex) => (prevIndex + 1) % cards.length);
      setSwipeDirection(null);
      setIsAnimating(false);
    }, 300);
  };

  // Handle previous card
  const prevCard = () => {
    if (isAnimating) return;
    setIsAnimating(true);
    setSwipeDirection('right');
    
    setTimeout(() => {
      setActiveIndex((prevIndex) => (prevIndex - 1 + cards.length) % cards.length);
      setSwipeDirection(null);
      setIsAnimating(false);
    }, 300);
  };

  // Handle dot navigation
  const goToCard = (index) => {
    if (isAnimating || index === activeIndex) return;
    setIsAnimating(true);
    setSwipeDirection(index > activeIndex ? 'left' : 'right');
    
    setTimeout(() => {
      setActiveIndex(index);
      setSwipeDirection(null);
      setIsAnimating(false);
    }, 300);
  };
  
  // Handle card click for navigation
  const handleCardClick = (card) => {
    // Don't allow navigation for unavailable cards
    if (card.status === 'coming-soon' || card.status === 'coming-soon') {
      return;
    }
    
    if (isMobile) {
      // On mobile, first click selects the card, second click navigates
      if (card.id - 1 === activeIndex) {
        if (card.url) {
          // External URL - open in new tab
          window.open(`https://${card.url}`, '_blank');
        } else if (card.path) {
          // Internal path - navigate
          navigate(card.path);
        }
      } else {
        goToCard(card.id - 1);
      }
    } else {
      // On desktop, directly navigate
      if (card.url) {
        // External URL - open in new tab
        window.open(`https://${card.url}`, '_blank');
      } else if (card.path) {
        // Internal path - navigate
        navigate(card.path);
      }
    }
  };

  // Card position function for mobile view - vertical stacking instead of horizontal
  const getCardStyle = (index) => {
    if (!isMobile) return {};
    
    // Calculate position relative to active card
    const position = index - activeIndex;
    
    // Apply different styles based on position - vertical stacking
    if (position === 0) {
      // Active card - centered
      return {
        transform: `translateY(0) scale(1)`,
        opacity: 1,
        zIndex: 10
      };
    } else if (position === 1) {
      // Next card - below and smaller
      return {
        transform: `translateY(20px) scale(0.9)`,
        opacity: 0,
        zIndex: 9
      };
    } else if (position === -1) {
      // Previous card - above and smaller
      return {
        transform: `translateY(-20px) scale(0.9)`,
        opacity: 0,
        zIndex: 9
      };
    } else {
      // All other cards - hidden
      return {
        transform: `scale(0.8)`,
        opacity: 0,
        zIndex: 8
      };
    }
  };
  
  // No touch events - using improved button navigation only

  // Handle keyboard navigation
  useEffect(() => {
    const handleKeyDown = (e) => {
      if (e.key === 'ArrowLeft') {
        prevCard();
      } else if (e.key === 'ArrowRight') {
        nextCard();
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [activeIndex, isAnimating]);

  // The getCardStyle function has been moved to the top of the component

  return (
    <div className="capsule-cards-container" ref={containerRef}>
      {/* Navigation buttons only shown on mobile */}
      {isMobile && (
        <>
          <button className="nav-arrow prev" onClick={prevCard} aria-label="Previous card">
            <span>‚Äπ</span>
          </button>
          <button className="nav-arrow next" onClick={nextCard} aria-label="Next card">
            <span>‚Ä∫</span>
          </button>
        </>
      )}
      
      {/* Cards container */}
      <div className={`cards-slider ${isMobile ? 'mobile' : 'desktop'} ${swipeDirection ? `swiping-${swipeDirection}` : ''}`}>
        {cards.map((card, index) => (
          <div 
            key={card.id}
            className={`capsule-card ${index === activeIndex ? 'active' : ''} ${card.status === 'coming-soon' || card.status === 'coming-soon' ? 'unavailable' : ''}`}
            style={{
              '--card-color': card.color,
              ...(isMobile ? getCardStyle(index) : {})
            }}
            onClick={() => handleCardClick(card)}
          >
            <div className="card-image" style={{ backgroundImage: `url(${card.image})` }}>
              <div className="card-overlay"></div>
              {(card.status === 'closed' || card.status === 'coming-soon') && (
                <div className="card-status-overlay">
                  <div className="status-badge">
                    {card.status === 'closed' ? 'COMING SOON' : 'COMING SOON'}
                  </div>
                </div>
              )}
              <h3 className="card-title">{card.title}</h3>
            </div>
          </div>
        ))}
      </div>
      
      {/* Dot indicators for mobile */}
      {isMobile && (
        <div className="carousel-dots">
          {cards.map((_, index) => (
            <span 
              key={index} 
              className={`dot ${index === activeIndex ? 'active' : ''}`}
              onClick={() => goToCard(index)}
            />
          ))}
        </div>
      )}
    </div>
  );
};

export default CapsuleCards;
